@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  background-color:  #faf0f1;
}

.logo-design {
  /* font-family: 'Guttman Yad-Brush'; */
  /* font-family: 'Guttman Mantova-Decor'; */
  font-family: 'Amatic SC', cursive;
  font-weight: bolder;
  font-size: 25px;
}

.font-bold {
  font-weight: bolder;
}

.jumbotron {
  padding: 2rem 1rem;
  margin-bottom: 2rem;
  background-color: #f8d7da;
  border-radius: .3rem;
  position: relative;
  overflow: hidden;
  /* background-image: url("https://openscholar.huji.ac.il/sites/default/files/styles/os_files_xxlarge/public/library/files/spryh_dp_rshy.png?m=1538923396&itok=1LUWiD7D");  */
}

.jumbotron h1 {
  position: relative;
  z-index: 2;
}

.jumbotron p {
  position: relative;
  z-index: 2;
}

.jumbotron::after {
  content: "";
  background: url(/my-app/static/media/books.35ffd38c.jpg);
  opacity: 0.3;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: block;
  position: absolute;
  z-index: 1;   

}

.card-design {
  margin-right: 30px;
  padding-bottom: 10px;
}

.hover-design:hover {
  color: #e94c59;
  cursor: pointer;
}

.name-of-book-design{
  color: #e94c59;
}

.image-border {
  width: 18rem;
  height: 27rem;
  border: 1px solid #e94c59;
  border-radius: 30px;
  margin-right: 100px;
}

.image {
  margin-right: 100px;
}

.link-design {
  color: blue;
  text-decoration: underline;
}

.link-design:hover {
  color: #e94c59;
  cursor: pointer;
}

.list-grout-design {
  margin-right: 20px;
}

.text-align-end{
  text-align: end;
}

.card-footer {
  border-top: 0px;
}

.select{
  border-radius: 30px; 
}

.form-group {
  position: relative;
}

.form-group .arrow {
  position: absolute;
  display: block;
  left: 15px;
  top: 10px;
  z-index: 1; 
} 

.footer {
  text-align: center;
  background-color:  #f8a8af;
  color:  #eb3c4a;
  font-weight: 500;
  padding-top: 20px;
  font-family: 'Cavolini';
  font-size: 20px;
}


.input-rounded-corners {
    border-radius: 30px;
}

 .button-rounded-corners {
    width: 100%;
    border-radius: 30px;
}

.search-book-design {
    margin-right: 20px;
    width: 100%; 
    border-radius: 30px;
    background-color: #f8d7da;
}

.brand-design {
    margin-right: 20px;
}

.login-design {
    margin-left: 20px;
}


.c-live-search-box {
    position: relative;
    z-index: 100;
}

.c-live-search-box .search-results {
    position: absolute;
    width: 100%;
}

.search-results:hover {
    cursor: pointer;
}

.list-group-item:hover{
    background-color: #f8d7da;
}
